const MAX_PIC_COUNT =  9
const MAX_CONTENT_LENGTH = 300
const db = wx.cloud.database()

import { write2cloudStroge } from '../../common/js/common.js'
export default {
	data() {
		return {
			albumArr: [],
			content: '',
			bottomDest: 0,
			userInfo: {}
		}
	},
	onLoad(options) {
		this.userInfo = options
		console.log(options)
	},
	methods: {
		chooseImg() {
			uni.chooseImage({
				count: MAX_PIC_COUNT - this.albumArr.length,
				sizeType: ['orginal', 'compressed'],
				sourceType: ['album', 'camera'],
				success: res => {
					if(res.tempFilePaths.length !== 0) {
						this.albumArr.push(...res.tempFilePaths)
					}
				},
			})
		},
		deleImg(index) {
			this.albumArr.splice(index, 1)
		},
		setKeyboradHeight(event) {
			this.bottomDest = event.detail.height
		},
		resetKeyboradHeight() {
			this.bottomDest = 0
		},
		onPreviewImg(index) {
			uni.previewImage({
				current: index,
				urls: this.albumArr
			})
		},
		successPublish() {
			uni.hideLoading()
			uni.showToast({
				icon: 'none',
				title: '发布成功'
			})
			uni.navigateBack({
				delta: 1
			})
		},
		async publish() {
			const content = this.content.trim()
			if(!content) {
				uni.showToast({
					icon: 'none',
					title: '内容不许为空'
				})
				return 
			}
			uni.showLoading({
				title: '上传loading',
				mask: true
			})
			const fileIDArr = await write2cloudStroge('newThings/', this.albumArr)
			db.collection('newThings').add({
				data: {
					...this.userInfo,
					fileIDArr,
					content,
					createdTime: db.serverDate(),
					favNum: 0
				}
			}).then(res => {
				this.successPublish()
				wx.cloud.callFunction({
					name: 'baiduAI',
					data: {
						$url: 'checkNewThing',
						fileIDArr,
						content,
						_id: res._id
					}
				}).then(res => {
					console.log(res)
					uni.showToast({
						icon: 'none',
						title: res.result.info
					})
				})
			}).catch(err => {
				uni.showToast({
					title: '上传失败'
				})
			})
		}
	},
	computed: {
		isShow() {
			return this.albumArr.length === MAX_PIC_COUNT
		}
	}
}
